export default {
	data() {
		return {
			competenceManage:{
				searchModel:{
					current: 1,
					pageSize: this.pageSize,
					total: 0
				},
				config:{
					noOptions: false,//
					noIndex: false,//
					title: '',//
				},
				definition:[{
					id: "name",
					name: "",
					type: "string",
					createable: true,
					editable: true,
					important: true
				},{
					id: "type",
					name: "",
					type: "select",
					render(data){
						switch(data) {
							case "0":
							return ""
							case "1":
							return ""
							case "2":
							return ""
						}
					},
					config: {
						options:[{
							key: "0",
							label: ""
						},{
							key: "1",
							label: ""
						},{
							key: "2",
							label: ""
						}]
					},
					createable: true,
					editable: true,
					important: true
				},{
					id: "des",
					name: "",
					type: "string",
					createable: true,
					editable: true
				},{
					id: "permission",
					name: "",
					type: "string",
					createable: true,
					editable: true,
					important: true
				}],
				objects:[],
				options:{
					create: {
						enable: true,
						action:(data)=>{
							if(!data.name || !data.permission || !data.type){
								this.$toast("")
								return
							}
							this.$store.commit('process')
							return new Promise((resolve, reject)=>{
								this.$axios.post('resource/insertResource', data)
								.then(res =>{
									this.$store.commit('done')
									resolve(res.data)
								}, err =>{
									this.$store.commit('done')
									reject(err)
								})
								.catch(err =>{
									this.$store.commit('done')
									reject(err)
								})
							})
						}
					},
					edit: {
						enable: true,
						action: (data)=>{
							if(!data.name || !data.permission || !data.type){
								this.$toast("")
								return
							}
							this.$store.commit('process')
							return new Promise((resolve, reject)=>{
								this.$axios.post('resource/updateResource', data)
								.then(res=>{
									this.$store.commit('done')
									resolve(res.data)
								}, err =>{
									this.$store.commit('done')
									reject(err)
								})
								.catch(err=>{
									this.$store.commit('done')
									reject(err)
								})
							})
						}
					},
					delete: {
						enable: true,
						action: (data)=>{
							this.$store.commit('process')
							return new Promise((resolve, reject)=>{
								this.$axios.delete('resource/deleteResource', {
									params:{
										id: data.id
									}
								})
								.then(res=>{
									this.$store.commit('done')
									resolve(res.data)
								}, err=>{
									this.$store.commit('done')
									reject(err)
								})
								.catch((err)=>{
									this.$store.commit('done')
									reject(err)
								})
							}) 
						}
					},
				}
			}
		}
	},
	methods:{
		/**
		 * 
		 */
		searchMompetenceList(searchModel) {
			this.$store.commit('process')
			this.$axios.get('resource/selectResource',{
				params: searchModel
			})
			.then((res)=>{
				this.$store.commit('done')
				if(res.data.code === 0){
					this.competenceManage.objects = res.data.data.list
					this.competenceManage.searchModel.total = res.data.data.total
				}else{
					this.$toast(res.data.msg)
				}
			})
			.catch((err)=>{
				this.$store.commit('done')
				this.$toast("")
			})
		}
	},
	mounted() {
		if(!this.$store.state.userId) 
			return
		this.searchMompetenceList(this.competenceManage.searchModel)
	}
}


