export default {
	data() {
		return {
			clientHeight: 0,
			clientWidth: 0,
			loginForm:{
				definition:[{
					id: "userName",
					name: "",
					type: "string",
					placeHolder: "/",
					validator(data){
						if(!data){
							this.$toast({
								message: "",
								duration: 1500
							})
							return false;
						}
						console.log(data);
						/// reg
						
						return true
					}
				},{
					id: "password",
					name: "",
					type: "password",
					placeHolder: ""
				}],
				model:{}
			}
		}
	},
	mounted () {
		this.clientHeight = `${window.screen.availHeight}`;
		this.clientWidth = `${window.screen.availWidth}`;
	},
	methods:{
		login() {
			console.log(this.loginForm.model)
			//
			// this.$axios.post();
		}
	}
}


