export default{
    data() {
        return {
            listIndex: 0,
            footerShow: true,
            showLoginDialog:false,
            iconColor: 0,
            banner: {
                src: '../static/images/about-banner.jpg',
                href: '',
                title: '',
                alt: 'banner'
            },
            hotProduct: {
                ChineseTitle: '',
                EnglishTitle: 'HOT PRODUCTS',
                product: [[{
                    src: '../../../static/images/hot-product1.jpg',
                    title: '',
                    date: '2017-11-17',
                    author: '',
                    description: ''
                },{
                    src: '../../../static/images/hot-product2.jpg',
                    title: '',
                    date: '2017-11-17',
                    author: '',
                    description: ''
                }],[
                    {
                    src: '../../../static/images/hot-product3.jpg',
                    title: '',
                    date: '2017-11-17',
                    author: '',
                    description: ''
                }
                ]]
            },
            allProduct: {
                //iconClass: ['icon-shouji','icon-diannao'],
                ChineseTitle: '',
                EnglishTitle: 'OUR PRODUCTS',
               // title: ['','PC'],
                title: [{
                name: 'PC',
                iconClass: 'icon-diannao'},{
                name: '',
                iconClass: 'icon-shouji'}],
                product: [
	            [{
	                src: '../../../static/images/read.jpg',
	                title: '',
	                date: '2017-11-17',
	                author: '',
	                description: ''
	            },{
	                src: '../../../static/images/crm.jpg',
	                title: 'CRM',
	                date: '2017-11-17',
	                author: '',
	                description: ''
	            },{
	                src: '../../../static/images/erp.jpg',
	                title: 'ERP',
	                date: '2017-11-17',
	                author: '',
	                description: ''
	            },{
	                src: '../../../static/images/voice.jpg',
	                title: '',
	                date: '2017-11-17',
	                author: '',
	                description: ''
	            },{
	                src: '../../../static/images/order.jpg',
	                title: '',
	                date: '2017-11-17',
	                author: '',
	                description: ''
	            }],[{
                src: '../../../static/images/smartApp.jpg',
                title: '',
                date: '2017-11-17',
                author: '',
                description: ''
            	}]]
            }
        }
    },
    methods: {
        productShow(index){
            this.listIndex = index;
            this.iconColor = index
        },
        toast(){
        	this.$toast("");
        	
        }
    },
    mounted(){
    	if(this.$route.query.key==0) {
    		var m = this.$refs.productTab.offsetTop-180;
    		window.scrollTo(0, m);
    	}else if(this.$route.query.key==1) {
    		var m = this.$refs.productTab.offsetTop-180;
    		window.scrollTo(0, m);
    		this.listIndex = 1;
    		this.iconColor = 1;
    	}
    },
    watch:{
    	$route(){
	     	if(this.$route.query.key==0) {
	     		var m = this.$refs.productTab.offsetTop-180;
    		    window.scrollTo(0, m);
	    		this.listIndex = 0;
	    		this.iconColor = 0;
	    	}else if(this.$route.query.key==1) {
	    		var m = this.$refs.productTab.offsetTop-180;
    		    window.scrollTo(0, m);
	    		this.listIndex = 1;
	    		this.iconColor = 1;
	    	}
	    }
    }
    	
}



