export default {
	data() {
		return {
			examineLog:{
				searchModel:{
					current: 1,
					pageSize: this.pageSize,
					total: 0
				},
				config:{
					noOptions: false,//
					noIndex: false,//
					title: '',//
				},
				definition:[{
					id: "id",
					name: "",
					type: "string"
				},{
					id: "oper",
					name: "",
					type: "string",
					searchable: true
				},{
					id: "cdate",
					name: "",
					type: "date",
					searchable: true
				},{	
					id: "opName",
					name: "()",
					type: "string"
				},{
					id: "opDsc",
					name: "",
					type: "string"
				}],
				objects:[],
				options:{
					detail:{
						enable: true,
						name: ""
					},
					search: {
						enable: true
					}
				}
			}
		}
	}, 
	methods:{
		/**
		 * 
		 */
		searchExamineLog(searchModel) {
			this.$store.commit('process')
			this.$axios.get('log/selectAll', {
				params: searchModel
			})
			.then((res)=>{
				console.log(res)
				this.$store.commit('done')
				if(res.data.code === 0){
					this.examineLog.objects = res.data.data.list
					this.examineLog.searchModel.total = res.data.data.total
				}else{
					this.$toast(res.data.msg)
				}
			})
			.catch((err)=>{
				this.$store.commit('done')
				this.$toast("")
			})
		}
	},
	mounted() {
		if(!this.$store.state.userId) 
			return
		if(this.$store.state.user.roles != "1") 
			return this.$toast("")

		this.searchExamineLog(this.examineLog.searchModel)
	}
}


