export default{
    data() {
        return {
            isShowDelet: false,
            listIndex: '',
            listIndex1: null,
            isHeader: false,
            maShow: false,
            headerPhone: {
                src: '../../../static/images/headerPhone.png'
            },
            headerName: {
                id: 1,
                name: '',
                title: ''
            },
            headerTitle: ['','',''],
            centerProduct1: [{
                src: '../../../static/images/centerProduct5.jpg',
                name: '',
                notes: ''
            },{
                src: '../../../static/images/centerProduct6.jpg',
                name: '',
                notes: ''
            }],
            centerProduct: [{
                src: '../../../static/images/centerProduct1.jpg',
                name: '',
                notes: '',
                staus: 0
            },{
                src: '../../../static/images/centerProduct2.jpg',
                name: '',
                notes: '',
                staus: 1
            },{
                src: '../../../static/images/centerProduct3.jpg',
                name: '',
                notes: '',
                staus: 0
            },{
                src: '../../../static/images/centerProduct4.jpg',
                name: '',
                notes: '',
                staus: 1
            },{
                src: '../../../static/images/centerProduct1.jpg',
                name: '',
                notes: '',
                staus: 0
            },{
                src: '../../../static/images/centerProduct2.jpg',
                name: '',
                notes: '',
                staus: 1
            },{
                src: '../../../static/images/centerProduct3.jpg',
                name: '',
                notes: '',
                staus: 0
            },{
                src: '../../../static/images/centerProduct4.jpg',
                name: '',
                notes: '',
                staus: 1
            }]
        }
    },
     methods:{
        closeDelet(index,){
            this.isShowDelet = true;
            this.listIndex = index;
            this.listIndex1 = index;
        },
        closeDialog(){
            this.isShowDelet = false;
            this.listIndex1 = null;
        },
        delet(){
            this.isShowDelet = false;
            this.centerProduct.splice(this.listIndex,1);
            this.listIndex1 = null;
        },
        header(){
            this.isHeader = true;
            this.maShow = true
        },
        close(){
            this.isHeader = false;
            this.maShow = false
        }
    }
}



