
export default{
	data() {
		return {
			tableTest:{
				config: {
					noOptions: false,//
					noIndex: false,//
					title: 'KdowTestTable',//
				},
				definition:[{
					id: "userName",
					name: "",
					type: "string",
					hiddenInTable: false,//
				},{
					id: "role",
					name: "",
					type: "select",
					config: {
						options:[{
							key: "admin",
							label: ""
						},{
							key: "inCount",
							label: ""
						},{
							key: "outCount",
							label: ""
						}]
					},
					render(data){
						if(data == 'admin'){
							return ''
						}else{
							return ''
						}
					}
				},{
					id: "lastLoginDate",
					name: "",
					type: "string",
					hiddenInTable: true
				},{
					id: "telphone",
					name: "",
					type: "string",
					editable: true,
					hiddenInTable: false
				},{
					id: "state",
					name: "",
					type: "string",
					render(data){
						return data === 'disabled'? '':''
					},
				}],
				objects:[],
				options:{
					detail:{
						enable: true,
						name: "",
					},
					edit: {
						enable: true,
						edit:(data)=> {
							//
							this.$toast({
								message: "..."
							})
							return true;
						}
					},
					delete:{
						enable: true,
						delete:(data)=> {
							//
							this.$alert("...")
							return true;
						}
					}
				}
			}
		}
	},
	methods: {
		getUsers() {
			this.$axios.get(this.baseUrl + 'user/getUser', {
				params: {
					userId: '001'
				}
			}).then((res)=>{
				console.log(res);
			})
		}
	},
	mounted() {
		this.getUsers();

		// this.$alert("...")
		// this.$toast({message: '', position: 'top'})
		this.$axios.get('/static/tableTest.json').then((res)=>{
			if(res.status === 200){
				this.$set(this.tableTest, 'objects', res.data);
			}
		})
	}
}


